<template>
    <div class="issue-information" v-loading="loading">
        <div class="issue-content">
            <el-form ref="addressForm" size="small" :model="addressForm" :rules="addressRules" label-width="100px" class="form-item">
                <el-form-item :label="$L('标题')+'：'" prop="title">
                    <el-input v-model="addressForm.title" :placeholder="$L(' 最多可添加100个字符')"
                              maxlength="100"></el-input>
                    <span class="font-grey">{{(addressForm.title).length}}/100</span>
                </el-form-item>
                <el-form-item :label="$L('缩略图')+'：'" class="img-item">
                    <img v-if="addressForm.img === 'http://bbx11.nicebox.test' || addressForm.img === '' "
                         src="../../../assets/img/distributor/addpt.jpg" border="0" class="up-show" align="absmiddle">
                    <img v-else :src="addressForm.img" border="0" class="up-show" align="absmiddle">
                    <input id="fileinp" type="file" name="CommentsPic" class="img-input" :placeholder="$L('上传图片')"
                            accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                </el-form-item>
                <el-form-item :label="$L('显示时间')+'：'" class="time-item">
                    <el-date-picker
                            value-format="yyyy-MM-dd HH:mm:ss"
                            v-model="addressForm.dtCreate"
                            default-value
                            type="datetime"
                            :placeholder="$L('选择日期')"
                            @change="SearchTimeFunc"
                            style="width: 200px">
                    </el-date-picker>
                    <span class="font-grey">{{ $L('显示给他人看的发布时间，默认为当前时间') }}</span>
                </el-form-item>
                <el-form-item :label="$L('描述')+'：'" prop="des">
                    <el-input v-model="addressForm.summary" type="textarea" maxlength="500" resize="none"
                              rows="4"></el-input>
                    <span class="font-grey">{{ addressForm.summary ? (addressForm.summary).length : 0 }}/500</span>
                </el-form-item>
                <el-form-item :label="$L('内容')+'：'" prop="content">
                    <quill-editor class="editor" maxlength="50000"
                                  v-model="addressForm.content"
                                  :options="editorOption">
                    </quill-editor>
                    <span class="font-grey">{{((addressForm.content).length - 7) < 0 ? 0 : (addressForm.content).length - 7}}/50000</span>
                </el-form-item>
            </el-form>
        </div>
        <div class="bottom-btns">
            <el-button @click="backFunc">{{ $L('返回') }}</el-button>
            <el-button type="primary" @click="saveInfoFun">{{ $L('发布') }}</el-button>
        </div>
        <div class="error" v-if="errorShow">
            <el-alert
                    class="error-tips"
                    :title="errorTitle"
                    :type="errorType"
                    :description="errorTips"
                    :closable="false"
                    :center="true"
                    show-icon>
            </el-alert>
        </div>
    </div>
</template>

<script>
    import SectionTitle from "../../../components/common/sectionTitle";

    export default {
        name: "issueInformation",
        components: {SectionTitle},
        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                id: 0,
                megInfo: {},
                editorOption: {},
                uploadImg: [],
                addressForm: {
                    title: '',
                    img: '',
                    dtCreate: '',
                    summary: '',
                    content: "",
                },
                showImgUrl: '',
                addressRules: {
                    title: [
                        {required: true, message: '请输入正确的标题'}
                    ],
                }
            }
        },
        beforeRouteUpdate (to, from, next) {
            this.getData(to);
            next();
        },

        created() {
            if (this.$route.params.hasOwnProperty('id')) {
                let id = this.$route.params.id;
                id = parseInt(id);
                if (isNaN(id)) {
                    id = 0
                }
                this.id = id;
            }
            if (this.id > 0) {
                this.getData();
            } else {
                this.loading = false;
            }
            this.addressForm.dtCreate =  this.formatDateTime(new Date());
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if(router){
                    console.log(router);
                }
                if (_self.id > 0) {
                    _self.loading = true;
                    _self.$api('readContentReleaseNews', {
                        id: _self.id
                    }, function (reData) {

                        if (reData.flag === 1) {
                            _self.megInfo = reData.data;
                            _self.addressForm.title = _self.megInfo.title;
                            _self.addressForm.dtCreate = _self.megInfo.dtCreate;
                            _self.addressForm.summary = _self.megInfo.summary;
                            _self.addressForm.content = _self.megInfo.content;
                            _self.addressForm.img = _self.megInfo.pic;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    });
                }
            },
            formatDateTime(date){
                let y = date.getFullYear();
                let m = date.getMonth() + 1;
                m = m < 10 ? ('0' + m) : m;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                let h = date.getHours();
                h=h < 10 ? ('0' + h) : h;
                let minute = date.getMinutes();
                minute = minute < 10 ? ('0' + minute) : minute;
                let second=date.getSeconds();
                second=second < 10 ? ('0' + second) : second;
                return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                let imgUrl = null;
                let size = Math.floor(files.size / 1024);
                let type = files.type.substring(6);
                if (size > 2 * 1024 * 1024) {
                    _self.$Message.info('请选择2M以内的图片！');
                    return false;
                }
                if (!(type === 'png' || type === 'jpeg' || type === 'jpg')) {
                    _self.$Message.info('请选择png、jpeg、jpg的图片格式！');
                    return false;
                }
                _self.uploadImg = {
                    'file': files
                };
                let reader = new FileReader();
                reader.readAsDataURL(files);
                reader.onload = function (evt) {
                    _self.addressForm.img = evt.target.result;
                }
            },
            SearchTimeFunc(time) {
                console.log(time);
                this.addressForm.dtCreate = time;
            },
            backFunc() {
                this.$router.push({name:'information'});
            },
            saveInfoFun() {
                const _self = this;
                _self.loading = true;
                if (_self.addressForm.title !== '') {
                    _self.addressForm.img = _self.uploadImg;
                    let arr = {
                        id: _self.id,
                        title: _self.addressForm.title,
                        dtCreate: _self.addressForm.dtCreate,
                        summary: _self.addressForm.summary,
                        content: _self.addressForm.content,
                        smallImg: _self.addressForm.img
                    };
                    let postData = _self.$objToFormData(arr);
                    // console.log("title", postData.get("title"));
                    _self.$api('editReleaseNews', postData, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                type: 'success',
                                message: _self.$L('发布成功!')
                            });
                            _self.$router.push({name:'information'});
                            _self.getData();
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }, true);
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('标题不能为空!')
                    });
                    _self.loading = false;
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";

    .issue-information {
        position: absolute;
        width: 100%;
        padding: 20px;
        left: 0;
        top: 0;
        .issue-content {
            padding: 20px;
            background: #fff;
            margin-top: 10px;

            .form-item {
                .font-grey {
                    padding-left: 10px;
                    color: #999;
                }
                /deep/ .el-input,/deep/ .el-textarea{
                    width: 92%;
                }
                .img-item {
                    .img-input {
                        width: 100px;
                        height: 100px;
                        position: absolute;
                        opacity: 0;
                        left: 0;
                        top: 0;
                        z-index: 9999;
                        cursor: pointer;
                    }
                    .up-show {
                        height: 100px;
                        width: 100px;
                        border: 1px solid #DBDBDB;
                    }
                }
            }
        }

        .bottom-btns {
            padding: 25px 9% 25px 20px;
            background: #fff;
            text-align: right;
            margin-bottom: 10px;
            button {
                padding: 13px 40px;
            }
        }

        .editor {
            position: relative;
            width: 92%;
            height: 800px;
            display: inline-block;
        }

        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>